<template>
  <div>
    <div class="row justify-content-center">
      <input type="text" id="blogSearch" placeholder="Search..." v-model="searchKey">
    </div>
    <div class="row align-items-center justify-content-center master-row">
        <div class="col-12 col-lg-6 col-md-6 col-sm-6">
          <div class="row">
            <div v-for="post in orderedPosts" :key="post.id" class="card col-12 mt-1 mb-1" @click="redirect(post.link)">
              <div class="card-header">
                <h5 class="card-title d-inline">{{ post.title }}</h5>
                <span class="float-right d-inline">{{ post.publishDate }}</span>
              </div>
              <div class="card-body">
                <p class="card-text" v-html="post.byline"></p>
              </div>
              <div class="card-footer">
                <span v-for="(tag, index) in post.tags" :key=index class="badge badge-dark">{{ tag }}</span>
              </div>
            </div>
          </div>
        </div>
    </div>
  </div>
</template>

<script>
export default {
  components: {
  },
  name: 'Blog',
  data () {
    return {
      searchKey: '',
      blogPosts: [
        {
          id: 0,
          title: 'Learning to Vue',
          byline: 'The first of many blog posts to come.',
          link: '/Blogposts/October-2020/Test-blog-post',
          image: '',
          tags: ['Vue'],
          publishDate: new Date('2020-10-23').toLocaleDateString('en-gb')
        },
        {
          id: 1,
          title: 'LINQ',
          byline: '',
          link: '',
          image: '',
          tags: ['C#'],
          publishDate: new Date('2020-10-21').toLocaleDateString('en-gb')
        },
        {
          id: 2,
          title: 'A beginner\'s guide to Razor',
          byline: '',
          link: '',
          image: '',
          tags: ['JavaScript', 'C#'],
          publishDate: new Date('2020-10-16').toLocaleDateString('en-gb')
        },
        {
          id: 3,
          title: 'SQL, JSON and you',
          byline: 'Using JSON to create an almost direct line of communication between your front-end and your database, without needing to transform your data at all, has made my life much easier. Here\'s how.',
          link: '/Blogposts/September-2020/Oldest-test-blog-post',
          image: '',
          tags: ['SQL', 'C#'],
          publishDate: new Date('2020-09-17').toLocaleDateString('en-gb')
        }
      ]
    }
  },
  computed: {
    orderedPosts () {
      let orderedPosts = this.blogPosts.filter(e => e.title.toLowerCase().includes(this.searchKey.toLowerCase())).sort((a, b) => b.date - a.date)
      return orderedPosts
    }
  },
  methods: {
    redirect (arg) {
      this.$router.push(arg)
    }
  }
}
</script>

<style scoped>
  .master-row {
    height: 90vh;
  }

  .card {
    border: 0;
    background-color: rgba(0,0,0,0.2);
    transition: 0.2s ease;
    cursor: pointer;
  }

  .card:hover {
    transform: translateY(-10px);
    background-color: rgba(0,0,0,0.5);
  }

  .card-footer {
    background-color: transparent;
  }

  .badge {
    margin-right: 1em;
  }

  #blogSearch {
    background-color: black;
    color: grey;
    border: 0;
    border-radius: 10px;
    margin-bottom: 1rem;
    height: 2.5rem;
    width: 20rem;
    padding: 1rem;
    -webkit-box-shadow: 0px 0px 10px 5px rgba(0,0,0,0.4);
    -moz-box-shadow: 0px 0px 10px 5px rgba(0,0,0,0.4);
    box-shadow: 0px 0px 10px 5px rgba(0,0,0,0.4);
}
</style>



