<template>
  <div>
    <div class="row justify-content-center">
      <input type="text" id="projectSearch" placeholder="Search..." v-model="searchKey">
    </div>
    <div class="row align-items-center justify-content-center master-row">
        <div class="col-12 col-lg-6 col-md-6 col-sm-6">
          <div class="row">
            <div v-for="project in orderedProjects" :key="project.id" class="card col-12 col-lg-4 col-md-4 mt-1 mb-1">
              <a :href="'//' + project.link" target="_blank"><img class="card-img-top mt-3" :src="project.image"></a>
              <div class="card-body">
                <h5 class="card-title">{{ project.name }}</h5>
                <p class="card-text" v-html="project.text"></p>
              </div>
              <div class="card-footer">
                <span v-for="(tag, index) in project.tags" :key=index class="badge badge-dark">{{ tag }}</span>
              </div>
            </div>
          </div>
        </div>
    </div>
  </div>
</template>

<script>
export default {
  components: {
  },
  name: 'Projects',
  data () {
    return {
      searchKey: '',
      projects: [
        {
          id: 0,
          name: 'BookClub',
          link: 'github.com/azadnalbandian/book-club',
          text: 'A tiny command-line utility for choosing a random book from a given user\'s Goodreads shelf.',
          image: require('../assets/Screenshot_1.jpg'),
          tags: ['C#']
        },
        {
          id: 1,
          name: 'MiDoS',
          link: 'midosweb.co.uk/',
          text: 'An NHS emergency services dashboard. Learn more about our client, Intuiti, at their <a href="http://www.intuiti.co.uk/">website.</a>',
          image: require('../assets/Screenshot_2.jpg'),
          tags: ['C#', 'Angular', 'JQuery', 'JavaScript', 'SQL Server']
        },
        {
          id: 2,
          name: 'PageSuite',
          link: 'www.pagesuite.co.uk',
          text: 'A PDF-based SaaS web app, offering digital newspaper and magazine replicas for many international clients including The Sun, the Boston Post, and the Dallas Morning News.',
          image: require('../assets/Screenshot_3.jpg'),
          tags: ['C#', 'SQL Server', 'JavaScript']
        }
      ]
    }
  },
  computed: {
    orderedProjects () {
      let orderedProjects = this.projects.filter(e => e.name.toLowerCase().includes(this.searchKey.toLowerCase())).sort((a, b) => a.name.localeCompare(b.name))
      return orderedProjects
    }
  }
}
</script>

<style scoped>
  .master-row {
    height: 90vh;
  }

  .card {
    border: 0;
    background-color: rgba(0,0,0,0.2);
    transition: 0.2s ease;
  }

  .card:hover {
    transform: translateY(-10px);
    background-color: rgba(0,0,0,0.5);
  }

  .card-footer {
    background-color: transparent;
  }

  img {
    border-radius: 5px;
    opacity: 0.8;
    transition: 0.2s ease;
    height: 300px;
  }

  img:hover {
    opacity: 1;
  }

  .badge {
    margin-right: 1em;
  }

  #projectSearch {
    background-color: black;
    color: grey;
    border: 0;
    border-radius: 10px;
    margin-bottom: 1rem;
    height: 2.5rem;
    width: 20rem;
    padding: 1rem;
    -webkit-box-shadow: 0px 0px 10px 5px rgba(0,0,0,0.4);
    -moz-box-shadow: 0px 0px 10px 5px rgba(0,0,0,0.4);
    box-shadow: 0px 0px 10px 5px rgba(0,0,0,0.4);
}
</style>



