<template>
    <div class="row align-items-center justify-content-center master-row">
        <div class="col-12 col-sm-6 col-md-6 col-lg-6 entry-element" @click="redirect">
            <div class="jumbotron">
                <div class="typewriter">
                    <h1 class="display-4">AZAD NALBANDIAN.</h1>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  name: 'Home',
  methods: {
    redirect () {
      // eslint-disable-next-line handle-callback-err
      this.$router.push('About').catch(err => {})
    }
  },
  mounted () {
    var dataText = ['I\'m Azad Nalbandian...', '...and this is my personal portfolio page.', 'I\'m a full-stack software engineer.', 'Click here to learn more about what I do.']
    function typeWriter (text, i, fnCallback) {
      if (i < (text.length)) {
        document.querySelector('h1').innerHTML = text.substring(0, i + 1) + '<span aria-hidden="true"></span>'

        setTimeout(function () {
          typeWriter(text, i + 1, fnCallback)
        }, 100)
      } else if (typeof fnCallback === 'function') {
        setTimeout(fnCallback, 700)
      }
    }
    function StartTextAnimation (i) {
      try {
        if (typeof dataText[i] === 'undefined') {
          setTimeout(function () {
            StartTextAnimation(0)
          }, 20000)
        }
        if (i < dataText[i].length) {
          typeWriter(dataText[i], 0, function () {
            StartTextAnimation(i + 1)
          })
        }
      } catch (error) {

      }
    }
    StartTextAnimation(0)
  }
}
</script>

<style scoped>
    .master-row {
        height: 90vh;
    }

    .entry-element {
        cursor: pointer;
    }

    .jumbotron {
        background-color: transparent;
    }

    h1 {
    font-size: 5em;
    color: white;
    }

    span {
        border-right: .05em solid;
        animation: caret 1s steps(1) infinite;
    }

    @keyframes caret {
    50% {
        border-color: transparent;
    }
    }
</style>



