                <h5>Welcome to my personal site.</h5>
                <p>
                    I'm a full-stack software engineer whose primary passions are backend and API development, mainly using C# and .NET Core (although this site was produced with Vue, my favourite JS framework). I'm also experienced in software architecture, front-end development, SQL, database administration, and the usage of the Oxford comma.<br><br>
                    This site contains a variety of projects I've worked on and blog posts I've written. I'm still learning, and I like to use writing to organise my thoughts on new subjects. Hopefully, my writings will be of use to others too.<br><br>
                    I'm currently an employee at a software development house, and was previously employed at a digital media company. I started working in software largely by accident; I'd been coasting from job to job, not really knowing what I wanted to do with myself, when I fell into a position as a first line support technician. My experience there inspired my ongoing passion for programming and web development. Since then, I've been fully committed to learning and improving my skillset, in order to attain further success within the industry.<br><br>
                    In my day job, I write and maintain code for clients, help other programmers write code, fix bugs, do a lot of Azure administration, and take naps during my lunch breaks. My long-term career goal is to decrease the amount of time I spend coding and fixing bugs, and significantly increase time spent napping.<br><br>
                    In a past life, I studied law at the University of Exeter. I wasn't much for it.<br><br>
                    I met my wonderful wife, Stacey, in 2016, and we were married in 2018. Right now, our family is just us and our pets (Pumpkin the cat and Luna the dog), but we're hoping to grow it in future.<br><br>
                    Beyond programming, my interests are many, but I'm especially dedicated to powerlifting. Currently, I hold a PR total of 1300lbs (420/355/525 S/B/D), and occasionally provide personal training.
                </p>
